import React from 'react' 
import './project.scss' 
import '../../node_modules/bootstrap/dist/css/bootstrap.min.css' 

function Projects() { 
    return ( 
        <div className="container projectContainer"> 
            <div className="d-flex mb-5"> 
                <img src="/static/images/projects.png" alt="logo"/>
                <h1>Projects</h1> 
            </div> 

            <div className="projects" style={{marginBottom: '30px'}}>  
                <div className="project1">  
                    <p style={{
                        background: 'rgb(246 246 246)', 
                        height: '50px', 
                        lineHeight: '50px', 
                        textAlign: 'center', 
                        color: 'rgb(80 80 80)', 
                        borderBottom: '1px solid rgb(210 210 210)'
                    }}> 
                        MERN Stack App
                    </p> 
                    <h4 style={{
                        height: '100px', 
                        lineHeight: '95px', 
                        textAlign: 'center'
                    }}>
                        Diet Planner
                    </h4>
                    <button className="sc btn btn-primary">
                        Source Code 
                    </button>
                    <button className="ld btn btn-success">
                        Live Demo
                    </button> 
                </div>
                <div className="project2">
                    <p>Hello2</p>
                </div>
                <div className="project3">
                    <p>Hello3</p>
                </div>
            </div> 
            <div className="card-deck"> 
                <div className="row">
                <div className="card col-lg-4 col-md-6 col-sm-12"> 
                    <div class="card-header">MERN Stack App</div>
                    <div className="card-body">
                        <h5 className="card-title">Diet Planner</h5>
                        <p className="card-text"><small class="text-muted">Last updated 3 mins ago</small></p>
                    </div> 
                </div> 
                <div className="card col-lg-4 col-md-6 col-sm-12">
                    <div className="card-body">
                        <h5 className="card-title">Card title</h5>
                        <p className="card-text"><small class="text-muted">Last updated 3 mins ago</small></p>
                    </div> 
                </div> 
                <div className="card col-lg-4 col-md-6 col-sm-12">
                    <div className="card-body">
                        <h5 className="card-title">Card title</h5>
                        <p className="card-text"><small class="text-muted">Last updated 3 mins ago</small></p>
                    </div> 
                </div> 
                </div>
            </div> 
        </div> 
    ) 
} 

export default Projects; 
